
import { Options, Vue } from 'vue-class-component'

@Options({
  props: {
    title: {
      type: String,
      default: '提示'
    },
    content: String,
    lock: {
      type: Boolean,
      default: true
    },
    ok: Function,
    close: Function
  }
})
export default class Alert extends Vue {
  content!: string
  lock!: boolean
  ok!: Function
  close!: Function

  top = 0
  left = 0

  width = 0
  height = 0

  clientWidth = 0
  clientHeight = 0
  moveX = 0
  moveY = 0

  _ok() {
    if (!this.ok || this.ok()) {
      this.$el.remove()
    }
  }

  _close() {
    if (!this.close || this.close()) {
      this.$el.remove()
    }
  }

  mousedown(event: MouseEvent) {
    this.moveX = event.pageX
    this.moveY = event.pageY
    document.onselectstart = function () {
      return false
    }
    document.onmousemove = (event: MouseEvent) => {
      const x = this.left + event.pageX - this.moveX
      const y = this.top + event.pageY - this.moveY
      if (x > 0 && x + this.width < this.clientWidth) {
        this.left = x
      }
      if (y > 0 && y + this.height < this.clientHeight) {
        this.top = y
      }
      this.moveX = event.pageX
      this.moveY = event.pageY
    }
    document.onmouseup = (event: MouseEvent) => {
      document.onmousemove = document.onmouseup = document.onselectstart = null
    }
  }

  position() {
    this.clientWidth = document.documentElement.clientWidth
    this.clientHeight = document.documentElement.clientHeight
    this.width = this.$el.offsetWidth
    this.height = this.$el.offsetHeight
    this.left = (this.clientWidth - this.width) / 2
    this.top = (this.clientHeight - this.height) / 2
  }

  mounted() {
    document.body.appendChild(this.$el)
    this.$nextTick(() => {
      this.position()
    })
    window.onresize = () => this.position()
  }
}
